body {
  background: #FFFFE0 url(/assets/bg.jpg); }

.navbar, .dropdown-menu {
  background: rgba(255, 255, 255, 0.25);
  border: none; }

.nav > li > a, .dropdown-menu > li > a:focus, .dropdown-menu > li > a:hover, .dropdown-menu > li > a, .dropdown-menu > li {
  border-bottom: 3px solid transparent; }

.nav > li > a:focus, .nav > li > a:hover, .nav .open > a, .nav .open > a:focus, .nav .open > a:hover, .dropdown-menu > li > a:focus, .dropdown-menu > li > a:hover {
  border-bottom: 3px solid transparent;
  background: none; }

.navbar a, .dropdown-menu > li > a, .dropdown-menu > li > a:focus, .dropdown-menu > li > a:hover, .navbar-toggle {
  color: #fff; }

.dropdown-menu {
  -webkit-box-shadow: none;
  box-shadow: none; }

.nav li:hover:nth-child(8n+1), .nav li.active:nth-child(8n+1) {
  border-bottom: #C4E17F 3px solid; }

.nav li:hover:nth-child(8n+2), .nav li.active:nth-child(8n+2) {
  border-bottom: #F7FDCA 3px solid; }

.nav li:hover:nth-child(8n+3), .nav li.active:nth-child(8n+3) {
  border-bottom: #FECF71 3px solid; }

.nav li:hover:nth-child(8n+4), .nav li.active:nth-child(8n+4) {
  border-bottom: #F0776C 3px solid; }

.nav li:hover:nth-child(8n+5), .nav li.active:nth-child(8n+5) {
  border-bottom: #DB9DBE 3px solid; }

.nav li:hover:nth-child(8n+6), .nav li.active:nth-child(8n+6) {
  border-bottom: #C49CDE 3px solid; }

.nav li:hover:nth-child(8n+7), .nav li.active:nth-child(8n+7) {
  border-bottom: #669AE1 3px solid; }

.nav li:hover:nth-child(8n+8), .nav li.active:nth-child(8n+8) {
  border-bottom: #62C2E4 3px solid; }

.navbar-toggle .icon-bar {
  color: #fff;
  background: #fff; }

.card {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s; }

/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2); }

/* Add some padding inside the card container */
.container {
  padding: 30px 5px; }

.btn-product {
  width: 100%; }

/* Search */
.search-form .form-group {
  float: right !important;
  transition: all 0.35s, border-radius 0s;
  width: 32px;
  height: 32px;
  background-color: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset;
  border-radius: 25px;
  border: 1px solid #ccc; }

.search-form .form-group input.form-control {
  padding-right: 20px;
  border: 0 none;
  background: transparent;
  box-shadow: none;
  display: block; }

.search-form .form-group input.form-control::-webkit-input-placeholder {
  display: none; }

.search-form .form-group input.form-control:-moz-placeholder {
  /* Firefox 18- */
  display: none; }

.search-form .form-group input.form-control::-moz-placeholder {
  /* Firefox 19+ */
  display: none; }

.search-form .form-group input.form-control:-ms-input-placeholder {
  display: none; }

.search-form .form-group:hover,
.search-form .form-group.hover {
  width: 100%;
  border-radius: 4px 25px 25px 4px; }

.search-form .form-group span.form-control-feedback {
  position: absolute;
  top: -1px;
  right: -2px;
  z-index: 2;
  display: block;
  width: 34px;
  height: 34px;
  line-height: 34px;
  text-align: center;
  color: #3596e0;
  left: initial;
  font-size: 14px; }

li.menu-logout {
  display: none; }

div.overlay {
  display: none; }

div.show-map h2 {
  text-align: center; }

div.show-map .map {
  height: 30em; }

div.show-map .description {
  font-family: Roboto;
  font-size: 15px;
  font-weight: 300; }

div.show-map .infowindow-content .title {
  font-weight: bold; }

div.show-map .infowindow-content {
  display: none; }

div.show-map .infowindow-content {
  display: inline; }

div.show-map .pac-card {
  margin: 10px 10px 0 0;
  border-radius: 2px 0 0 2px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  outline: none;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  background-color: #fff;
  font-family: Roboto; }

div.show-map .pac-container {
  padding-bottom: 12px;
  margin-right: 12px; }

div.show-map .pac-controls {
  display: inline-block;
  padding: 5px 11px; }

div.show-map .pac-controls label {
  font-family: Roboto;
  font-size: 13px;
  font-weight: 300; }

div.show-map .pac-input {
  background-color: #fff;
  font-family: Roboto;
  font-size: 15px;
  font-weight: 300;
  margin-left: 12px;
  padding: 0 11px 0 13px;
  text-overflow: ellipsis;
  width: 400px; }

div.show-map .pac-input:focus {
  border-color: #4d90fe; }

div.show-map .title {
  color: #fff;
  background-color: #4d90fe;
  font-size: 25px;
  font-weight: 500;
  padding: 6px 12px; }

div.show-map .target {
  width: 345px; }

div.show-map .canvas {
  border: 1px solid black;
  margin-top: 10px; }

div.show-map img.location-image {
  max-width: 5em;
  max-height: 5em; }
